import warnings from './warnings'
import cn from './classNames'

const body = document.getElementsByTagName('body')
const modal = document.querySelector('.js-modal')
const form = document.querySelector('.js-form')
const inputs = document.querySelectorAll('.js-input')
const span = document.querySelectorAll('.js-span')

// —Å–æ—Å—Ç–æ—è–Ω–∏–µ –∫–æ–Ω—Ç–µ–Ω—Ç–∞ –≤ –ø–æ–ø–∞–ø—á–∏–∫–∞—Ö

let modalText = `${warnings.warn} –ø–æ–ª—è`

// –ø–æ–∫–∞–∑—ã–≤–∞–µ–º body –ø–æ—Å–ª–µ –ø–æ–ª–Ω–æ–π –∑–∞–≥—Ä—É–∑–∫–∏ —Å—Ç—Ä–∞–Ω–∏—Ü—ã

window.addEventListener('load', () => (body[0].style.opacity = 1))

// —Å–∞–±–º–∏—Ç —Ñ–æ—Ä–º—ã

form.addEventListener('submit', (evt) => {
    evt.preventDefault()

    showModal(modalText)
})

// –¥–µ–ª–∞–µ–º –∏–∑ –ø—Å–µ–≤–¥–æ–º–∞—Å—Å–∏–≤–∞ –º–∞—Å—Å–∏–≤ –∏ –ø–µ—Ä–µ–±–∏—Ä–∞–µ–º, –Ω–∞—Ö–æ–¥–∏–º –Ω–∞—à–∏ –µ–ª–µ–º–µ–Ω—Ç—ã –∏–∑ DOM –∏ –≤—ã–∑—ã–≤–∞–µ–º —Ñ—É–Ω-—Ü–∏—é –≤–∞–ª–∏–¥–∞—Ü–∏–∏ –ø—Ä–∏ –∫–∞–∂–¥–æ–º —Ä–∞—Å—Ñ–æ–∫—É—Å–µ

Array.from(inputs).forEach((input, i, arr) => {
    input.addEventListener('change', handleInput)

    if (input.value === '') modalText = `${warnings.warn} –ø–æ–ª—è`

    function handleInput(evt) {
        let input = evt.target

        validation(input, span[i])

        // —Ç–µ–∫—Å—Ç –¥–ª—è –ø–æ–ø–∞–ª–∞ —Å –æ—à–∏–±–∫–æ–π –∏–ª–∏ –±–µ–∑

        modalText = arr.every((a) => !a.className.includes('error'))
            ? warnings.success
            : warnings.error

        // —Ç–µ–∫—Å—Ç –¥–ª—è –ø–æ–ø–∞–ø–∞ –µ—Å–ª–∏ –æ–¥–Ω–æ –∏–∑ –ø–æ–ª–µ–π –ø—É—Å—Ç–æ–µ

        // if (input.value === '') {
        //     console.log(inputs[i].name)
        // }

        // if (arr.some((a) => a.value === '')) {
        //     const emptyInputName = arr.filter((a) => a.value === '')[0]?.name
        //     modalText = `${warnings.warn} –ø–æ–ª–µ ${emptyInputName}`
        // }

        // if (input.value === '') {
        //     if (input.name === 'name') {
        //         console.log('name')
        //     }
        //     if (input.name === 'email') {
        //         console.log('email')
        //     }
        //     if (input.name === 'domain') {
        //         console.log('domain')
        //     }
        // }

        // switch (input.name) {
        //     case input['name'] === '':
        //         alert(input.name)
        //         break
        //     case input['email'] === '':
        //         alert(input.name)
        //         break
        //     case input['domain'] === '':
        //         alert(input.name)
        //         break
        //     default:
        //         break
        // }

        // —Ç–µ–∫—Å—Ç –¥–ª—è –ø–æ–ø–∞–ª–∞ –µ—Å–ª–∏ –æ–±–∞ –ø–æ–ª—è –ø—É—Å—Ç—ã—Ö
    }
})

// –ø—Ä–æ–≤–µ—Ä—è–µ–º –¥–ª–∏–Ω–Ω—É –Ω–∞–±—Ä–∞–Ω–æ–≥–æ —Ç–µ–∫—Å—Ç–∞ –∏ –Ω–∞ –ª–∞—Ç—ã–Ω–∏—Ü—É

function validation(input, span) {
    const inputValueLength = input.value.length

    if (inputValueLength === 0) {
        input.classList.remove(cn.inputError)
        modalText = warnings.warn
    } else if (inputValueLength < 2 || inputValueLength > 10) {
        input.classList.add(cn.inputError)
        span.innerText = `–í –ø–æ–ª–µ ${input.name} –æ—Ç 2 –¥–æ 10 —Å–∏–º–≤–æ–ª–æ–≤`
    } else {
        input.classList.remove(cn.inputError)
    }

    if (input.value.match(/[–ê-—è–Å—ë]/)) {
        input.classList.add(cn.inputError)
        span.innerText = `–¢–æ–ª—å–∫–æ –ª–∞—Ç–∏–Ω–∏—Ü–∞`
    }
}

// —Ñ—É–Ω-—Ü–∏—è –æ—Ç–≤–µ—á–∞—é—â–∞—è –∑–∞ –ø–æ–ø–∞–ø—ã —Å –ø—Ä–µ–¥—É–ø—Ä–µ–∂–¥–µ–Ω–∏—è–º–∏

function showModal(value) {
    modal.innerHTML = value
    modal.classList.add(cn.modalActive)
    removeClass(modal, cn.modalActive)

    if (value === warnings.error) {
        modal.classList.add(cn.modalError)
        removeClass(modal, cn.modalError)
    }
    if (value === warnings.warn) {
        modal.classList.add(cn.modalWarn)
        removeClass(modal, cn.modalWarn)
    }
    if (value === warnings.success) {
        inputs.forEach((input) => (input.value = ''))
        modalText = `${warnings.warn} –ø–æ–ª—è`
        modal.classList.add(cn.modalSuccess)
        removeClass(modal, cn.modalSuccess)
    }
}

// —É–¥–∞–ª—è–µ–º –∫–ª–∞—Å—ã —á–µ—Ä–µ–∑ –∫–∞–∫–æ–µ-—Ç–æ –≤—Ä–µ–º—è

function removeClass(item, className) {
    setTimeout(() => {
        item.classList.remove(className)
    }, 2000)
}
